import axios from "axios";

export const truncateAddress = (address) => {
  if (!address) return "No Account";
  return `${address.slice(0, 6)}${address.slice(-4)}`;
};

export const toHex = (num) => {
  const val = Number(num);
  return "0x" + val.toString(16);
};

export const RPC_OPERATIONS = {
  "CALL": "call",
  "SEND": "send",
  "ESTIMATE": "estimateGas"
};

export const rpc = (contract, method, params, operation, options) =>
  contract.methods[method](...params)[operation](options);

export const deBankUserTokensListApi = (address) =>
  axios.get(`https://openapi.debank.com/v1/user/token_list?id=${address}&chain_id=eth&is_all=false`, { headers: { 'accept': 'application/json' } });

export const deBankUserBalanceApi = (address) =>
  axios.get(`https://openapi.debank.com/v1/user/chain_balance?id=${address}&chain_id=eth`, { headers: { 'accept': 'application/json' } });

export const cryptoCompareETHPriceApi = () =>
  axios.get(`https://min-api.cryptocompare.com/data/price?fsym=ETH&tsyms=USD&api_key=${process.env.REACT_APP_CRYPTOCOMPARE_API_KEY}`);
