import { useState } from "react"

import { useDropzone } from "react-dropzone";

export function SubmitPage({ tableData, callback }) {
  const [txId, setTxId] = useState("");
  const [lostAmount, setLostAmount] = useState("");
  const [expAddress, setExpAddress] = useState("");
  const [submittedAddress, setSubmittedAddress] = useState("");
  const [submittedDate, setSubmittedDate] = useState(() => {
    let rightNow = new Date();
    let res = rightNow.toISOString().slice(0, 10);
    return res;
  });

  const { acceptedFiles, getRootProps, getInputProps } = useDropzone();

  const files = acceptedFiles.map((file) => (
    <li key={file.path}>
      {file.path} - {file.size} bytes
    </li>
  ));

  const handleCallback = (event) => {
    callback(event)
  }

  return (
    <div>
      <div className="row mt-10">
        <div className="col-md mr-30">
          <p className="fs-18 title-color mb-10">Transaction ID</p>
          <p className="fs-14 mb-10">
            Input transaction ID used in exploit
          </p>
          <input
            className="input-box"
            type="number"
            value={txId}
            onChange={(e) => setTxId(e.target.value)}
          />
        </div>
        <div className="col-md mr-30">
          <p className="fs-18 title-color mb-10">Amount Lost</p>
          <p className="fs-14 mb-10">Input amount lost in event</p>
          <input
            className="input-box"
            type="number"
            value={lostAmount}
            onChange={(e) => setLostAmount(e.target.value)}
          />
        </div>
        <div className="col-md">
          <p className="fs-18 title-color mb-10">Exploiter Address</p>
          <p className="fs-14 mb-10">
            Submit address associated with exploiter
          </p>
          <input
            className="input-box"
            value={expAddress}
            onChange={(e) => setExpAddress(e.target.value)}
          />
        </div>
      </div>
      <div className="row mt-30">
        <div className="col-md-8 mr-30 textarea-wrapper">
          <textarea
            className="input-box textarea"
            placeholder="Provide additional info here"
          />
        </div>
        <div className="col-md fileUpload-wrapper">
          <div {...getRootProps({ className: "dropzone" })}>
            <input {...getInputProps()} />
          </div>
        </div>
      </div>
      <div className="mt-30 d-flex flex-row flex-wrap">
        <div
          className="d-flex flex-column mr-30"
          style={{ width: "170px" }}
        >
          <p className="fs-18 title-color mb-10">Date Submitted</p>
          <input
            type="date"
            className="input-box"
            value={submittedDate}
            onChange={(e) => setSubmittedDate(e.target.value)}
          />
        </div>
        <div className="d-flex flex-column" style={{ width: "250px" }}>
          <p className="fs-18 title-color mb-10">Address Submitting</p>
          <input
            className="input-box"
            value={submittedAddress}
            onChange={(e) => setSubmittedAddress(e.target.value)}
          />
        </div>
      </div>
      <div className="d-flex justify-content-end">
        <button className="submit">Submit</button>
      </div>
    </div>
  )
}