import React, { useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom'

import * as IconModule from '../assets/imgs/icons'

import '../assets/scss/Footer.scss'


const navArr = [
  { path: '/dashboard', title: 'Dashboard' },
  { path: '/stake', title: 'Stake' },
  { path: '/membership', title: 'Membership' },
  { path: '/events', title: 'Events' },
  { path: '/governance', title: 'Governance' },
]

const Footer = () => {

  const { pathname } = useLocation()

  useEffect(() => {
    const navBar = document.getElementById("nav");
    const cursor = document.getElementById("cursor");
    const navItems = navBar.querySelectorAll('#navItems');
    navItems.forEach((navItem) => {
      navItem.addEventListener('click', function (event) {
        const { offsetLeft, clientWidth } = event.target.closest('div#navItems');
        cursor.style.left = offsetLeft + 'px';
        cursor.style.width = clientWidth + 'px';
      });
    });
    const currentNavItems = document.querySelectorAll('#navItems')

    // check activated item 
    let i;
    if (pathname === '/') i = 0;
    else {
      for (i = 0; i < navArr.length; i++) {
        if (pathname.indexOf(navArr[i].path) > -1) {
          break
        }
      }
    }
    const { offsetLeft, clientWidth } = currentNavItems[i];
    cursor.style.left = offsetLeft + 'px';
    cursor.style.width = clientWidth + 'px';
  }, [])

  const checkActivated = (path) => {
    return !pathname.indexOf(path)
  }

  return (
    <div className='Footer-Wrapper'>
      <div className='Footer' id='nav'>
        {
          navArr.map((nav, index) => {
            const Icon = IconModule[nav.title]
            return (
              <div key={index} id='navItems'>
                <Link to={nav.path} key={index} className='prevent-cursor'>
                  <div className={`nav ${checkActivated(nav.path) ? 'nav-active' : ''}`}>
                    <Icon color={checkActivated(nav.path) ? '#4569F5' : ''} className='icon' />
                    {/* <span style={{ 'marginLeft': '8px', 'fontFamily': 'AileronLight' }}>{nav.title}</span> */}
                    <span className='nav-title'>{nav.title}</span>
                  </div>
                </Link>
              </div>
            )
          })
        }
      </div>
      <div className='cursor' id='cursor'></div>
    </div>
  )
}

export default Footer