import { useState, useEffect } from "react"

import SortDown from "../../assets/imgs/ArrowDropDownIcon.svg";
import SortUp from "../../assets/imgs/ArrowDropUpIcon.svg";
import ThreeDots from "../../assets/imgs/ThreeDots.svg";
import Approve from "../../assets/imgs/Approve.svg";
import Deny from "../../assets/imgs/Deny.svg";

export function AssessorTable({ tableData, callback }) {
  const [isLossDateSort, setIsLossDateSort] = useState(true);
  const [isAllDateSort, setIsAllDateSort] = useState(true);

  const [isLossClaimSort, setIsLossClaimSort] = useState(true);
  const [isAllClaimSort, setIsAllClaimSort] = useState(true);

  const [LossTable, setLossTable] = useState(tableData)
  const [AllTable, setAllTable] = useState(tableData)

  useEffect(() => {
    let temp = tableData.map((t) => t);
    temp.sort(function (a, b) {
      return isLossClaimSort
        ? a.claim_status - b.claim_status
        : b.claim_status - a.claim_status;
    });
    setLossTable(temp);
  }, [isLossClaimSort]);

  useEffect(() => {
    let temp = tableData.map((t) => t);
    temp.sort(function (a, b) {
      return isAllClaimSort
        ? a.claim_status - b.claim_status
        : b.claim_status - a.claim_status;
    });
    setAllTable(temp);
  }, [isAllClaimSort]);

  const handleCallback = (event, fromEvent, data) => {
    callback(event, fromEvent, data)
  }

  return (
    <>
      <div className="fairside-table table-responsive p-3 border-05 radius-10 mb-30">
        <table className="table table-hover">
          <thead>
            <tr>
              <td>Loss Events</td>
              <td>Action</td>
              <td
                onClick={() => setIsLossDateSort(!isLossDateSort)}
                className="cursor-point"
              >
                Date Created{" "}
                <img
                  src={isLossDateSort ? SortDown : SortUp}
                  alt=""
                  className="ml-5"
                />
              </td>
              <td
                onClick={() => setIsLossClaimSort(!isLossClaimSort)}
                className="cursor-point"
              >
                Status{" "}
                <img
                  src={isLossClaimSort ? SortDown : SortUp}
                  alt=""
                  className="ml-5"
                />
              </td>
            </tr>
          </thead>
          <tbody>
            {LossTable.map((t, index) => (
              <tr key={index}>
                <td>
                  <img src={t.logo} alt="" className="mr-10" />
                  {t.project}
                </td>
                <td onClick={() => console.log("abc")}>
                  <p
                    className="pri-color mb-0 cursor-point"
                    onClick={() => handleCallback("assessor-csrview", 'assessor',t)}
                  >
                    View
                  </p>
                </td>
                <td>{t.created_at}</td>
                <td>
                  <img
                    src={
                      t.claim_status === 0
                        ? ThreeDots
                        : t.claim_status === 1
                          ? Deny
                          : Approve
                    }
                    alt=""
                    className="mr-10"
                  />
                  {t.claim_status === 2
                    ? "Claimable"
                    : t.claim_status === 1
                      ? "Not Claimable"
                      : "Pending Secondary Assessment"}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        <div
          className="view-all fs-16 title-color cursor-point text-center"
          onClick={() => handleCallback("all")}
        >
          View All Loss Events
        </div>
      </div>

      <div className="fairside-table table-responsive p-3 border-05 radius-10">
        <table className="table table-hover">
          <thead>
            <tr>
              <td>Claims/Cost Share Request</td>
              <td>Action</td>
              <td
                onClick={() => setIsAllDateSort(!isAllDateSort)}
                className="cursor-point"
              >
                Date Created{" "}
                <img
                  src={isAllDateSort ? SortDown : SortUp}
                  alt=""
                  className="ml-5"
                />
              </td>
              <td
                onClick={() => { }}
                className="cursor-point"
              >
                Associated Loss Event{" "}
                <img
                  src={isLossClaimSort ? SortDown : SortUp}
                  alt=""
                  className="ml-5"
                />
              </td>
              <td
                onClick={() => setIsAllClaimSort(!isAllClaimSort)}
                className="cursor-point"
              >
                Status{" "}
                <img
                  src={isAllClaimSort ? SortDown : SortUp}
                  alt=""
                  className="ml-5"
                />
              </td>
            </tr>
          </thead>
          <tbody>
            {AllTable.map((t, index) => (
              <tr key={index}>
                <td>
                  <img src={t.logo} alt="" className="mr-10" />
                  {t.request}
                </td>
                <td onClick={() => console.log("abc")}>
                  <p
                    className="pri-color mb-0 cursor-point"
                    onClick={() => handleCallback("assessor-lossview", 'assessor', t)}
                  >
                    View
                  </p>
                </td>
                <td>{t.created_at}</td>
                <td>{t.project}</td>
                <td>
                  <img
                    src={
                      t.claim_status === 0
                        ? ThreeDots
                        : t.claim_status === 1
                          ? Deny
                          : Approve
                    }
                    alt=""
                    className="mr-10"
                  />
                  {t.claim_status === 2
                    ? "Claimable"
                    : t.claim_status === 1
                      ? "Not Claimable"
                      : "Pending Secondary Assessment"}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </>
  )
}