import React, { useState } from 'react'
import { useHistory } from 'react-router-dom';
import ReactApexChart from 'react-apexcharts';


import '../assets/scss/Membership.scss'
import Available from '../assets/imgs/Available.svg'
import Unavailable from '../assets/imgs/Unavailable.svg'
import Copy from '../assets/imgs/Copy.svg'
import ETHOFNFTCARD from '../assets/imgs/ETHOFNFTCARD.svg'
import NFTCard from '../assets/imgs/NFTCard.svg'

import ConvictionBoard from '../components/ConvictionBoard';
import ToolTip from '../components/ToolTip';
import Modal from '../components/Modal';

const Default_State = {
  series: [25],
  options: {
    chart: {
      height: 300,
      type: 'radialBar',
    },
    plotOptions: {
      radialBar: {
        hollow: {
          margin: 0,
          size: '60%',
        },
        track: {
          background: '#d7d7d7'
        },
        dataLabels: {
          show: true,
          name: {
            offsetY: -10,
            show: true,
            color: '#888',
            fontSize: '17px'
          },
          value: {
            // formatter: function (val) {
            //   return parseInt(val);
            // },
            color: '#5a6169',
            fontSize: '36px',
            // fontWeight: '600',
            show: true,
          }
        }
      }
    },
    fill: {
      colors: ['#4569f5']
    },
    labels: ['Available'],
  },


};

const SignUpModalStyle = {
  padding: '',
  background: 'linear-gradient(90deg, #4569F5 0%, #4A6DF5 3%, #6885F7 29%, #7E97F8 54%, #8BA1F9 78%, #8FA5F9 100%)',
  border: '',
}


const MembershipSign = () => {

  const history = useHistory()
  const topUp = () => {
    history.push('/membershipsign')
  }

  const [copied, setCopied] = useState(false)
  const [isOpen, setIsOpen] = useState(false)

  const CopyPaste = (content) => {
    setCopied(true)
    navigator.clipboard.writeText(content)
    setTimeout(() => {
      setCopied(false)
    }, 1000);
  }

  return (
    <div className='Membership'>
      <div className='row'>
        <div className='col-lg-4 body-card mr-30'>
          <p className='fs-26 title-color semi-bold mb-30'>Remaining Coverage</p>
          <p className='text-color fs-18'>March 1, 2021 - March 1, 2022</p>
          <div className='d-flex justify-content-center'>
            <ReactApexChart options={Default_State.options} series={Default_State.series} type="radialBar" height={300} />
          </div>
          <div className='d-flex justify-content-between'>
            <p className='mb-0 ml-30'><img src={Available} alt='' className='mr-10' />25% Available</p>
            <p className='mb-0 mr-30'><img src={Unavailable} alt='' className='mr-10' />75% Unavailable</p>
          </div>
        </div>
        <div className='col-lg-4 mr-30 mt-md-3 mt-lg-0 body-card d-flex flex-column justify-content-between'>
          <p className='fs-26 title-color semi-bold mb-10'>Membership</p>
          <p className='fs-16 text-color mb-30'>Overview of your memership ID, stats and total coverage</p>
          <div className='d-flex justify-content-between mb-10'>
            <p className='fs-16 title-color'>Membership ID: </p>
            <div className='fs-16 text-color'>158215CFSD
              <ToolTip content="Yee-haw!" direction="bottom" copyBoard copyPaste={copied}>
                <img src={Copy} alt='' className='ml-10' onClick={() => CopyPaste('158215CFSD')} />
              </ToolTip>
            </div>
          </div>
          <div className='d-flex justify-content-between mb-10'>
            <p className='fs-16 title-color'>Total Claims: </p>
            <p className='fs-16 text-color'>145</p>
          </div>
          <div className='d-flex justify-content-between mb-10'>
            <p className='fs-16 title-color'>Total ETH Payout: </p>
            <p className='fs-16 text-color'>54 ETH</p>
          </div>
          <div className='d-flex justify-content-between mb-10'>
            <p className='fs-16 title-color'>Total USD Payout: </p>
            <p className='fs-16 text-color'>$335,614</p>
          </div>
          <div className='d-flex justify-content-between mb-10'>
            <p className='fs-16 title-color'>Remaining Coverage: </p>
            <p className='fs-16 text-color'>374 Days</p>
          </div>
          <div className='d-flex justify-content-between mb-10'>
            <p className='fs-16 title-color'>Membership Card: </p>
            <p className='fs-16 pri-color cursor-point' onClick={() => setIsOpen(true)}>View</p>
          </div>
          {/* <div className='d-flex justify-content-end'>
            <button className='border-05 radius-10 bg-card top_up' onClick={() => topUp()}>Top Up On Coverage</button>
          </div> */}
        </div>
        <div className='col-lg mt-md-3 mt-lg-0 body-card d-flex flex-column text-center justify-content-between'>
          <p className='fs-26 title-color semi-bold mb-30'>Membership</p>
          <p className='pri-color bold fs-26'>213,511</p>
          <p>You have 2% of the total
            conviction score pool.</p>
          {/* <div>
            <p className='mt-0'>7 Day Change</p>
            <p className='pri-color fs-21 bold'>4531.4+</p>
          </div> */}
          <p className='fs-26 title-color semi-bold mb-30'>Governance</p>
          <div>
            <div className='progress'>
              <div className='progress-bar' style={{ 'width': '56%' }}></div>
            </div>
            <p className='fs-16 mt-10'>213,511/500,000</p>
          </div>
          <button className='border-05 radius-10 bg-card top_up'>Mint NFT</button>
        </div>
      </div>
      <div className='row mt-30'>
        <div className='col-lg-4 body-card mr-30 d-flex flex-column text-center justify-content-between'>
          <p className='fs-26 title-color semi-bold mb-30'>Remaining Days</p>
          <p className='pri-color bold fs-26'>374 Days</p>
          <button className='border-05 radius-10 bg-card top_up' onClick={() => topUp()}>Top Up On Coverage</button>
        </div>
        <div className='col-lg mt-md-3 mt-lg-0 body-card d-flex flex-column justify-content-between'>
          <div className='d-flex justify-content-between mb-10'>
            <div>
              <p className='fs-26 title-color semi-bold mb-10'>Rewards Pool</p>
              <p className='fs-16 text-color mb-30'>Claims your rewards here</p>
            </div>
            <div>
              <button className='border-05 radius-10 bg-card top_up'>Claim All</button>
            </div>
          </div>
          <div className='d-flex justify-content-between mb-10'>
            <p className='fs-16 title-color'>Tribute Rewards</p>
            <p className='fs-16 text-color'>145</p>
          </div>
          <div className='d-flex justify-content-between mb-10'>
            <p className='fs-16 title-color'>Staking</p>
            <p className='fs-16 text-color'>145</p>
          </div>
          <div className='d-flex justify-content-between mb-10'>
            <p className='fs-16 title-color'>Governance</p>
            <p className='fs-16 text-color'>145</p>
          </div>
          <div className='d-flex justify-content-between mb-10'>
            <p className='fs-16 title-color'>Governance</p>
            <p className='fs-16 text-color'>145</p>
          </div>
        </div>

      </div>
      {/* <div className="mt-30 bg-card border-05 radius-10 p-20">
        <ConvictionBoard />
      </div> */}
      {/* <Modal isOpen={isOpen} setIsOpen={setIsOpen} content_style={SignUpModalStyle}>
        <div className='nft-card'>
          <div className='d-flex flex-row'>
            <h3></h3>
          </div>
          <img src={NFTCard} alt='' />
        </div>
      </Modal> */}
    </div >
  )
}

export default MembershipSign



