import React, { useEffect, useState } from "react";
import Select from "../components/Select";
import Modal from "../components/Modal";

import "../assets/scss/Events.scss";
import Confirm from "../assets/imgs/ConfirmIcon.svg";

import { table_data, table_assessor_data, table_vote_data } from "../config/variables";
import { AllTable } from "../components/Events/AllTable";
import { VoteTable } from "../components/Events/VoteTable";
import { SubmitPage } from "../components/Events/SubmitPage";
import { VoteEventTable } from "../components/Events/VoteEventTable";
import { ApprovedTable } from "../components/Events/ApprovedTable";
import { ApprovedEventTable } from "../components/Events/ApprovedEventTable";
import { AssessorTable } from "../components/Events/AssessorTable";
import { AssessorCSRView } from "../components/Events/AssessorCSRView";
import { AssessorLossView } from "../components/Events/AssessorLossView";

const Events = () => {
  const [status, setStatus] = useState("all");
  const [fromStatus, setFromStatus] = useState('all')
  const [detailItem, setDetailItem] = useState()
  const [isOpen, setIsOpen] = useState(false);

  const handleSelect = (option) => {
    setStatus(option.value);
  };
  const handleTableCallback = (event, fromEvent, data) => {
    setStatus(event);
    setFromStatus(fromEvent)
    setDetailItem(data)
  };

  const StatusTitle = () => {
    switch (status) {
      case 'submit':
        return 'Submit Event';
      case 'vote':
        return 'Vote';
      case 'vote-event':
        return 'Vote';
      case 'approved':
        return 'Approved Events';
      case 'approve-event':
        return 'Make a Claim';
      case 'assessor':
        return 'Cost Share Request';
      case 'assessor-csrview':
        return 'Vote';
      default:
        return 'Events'
    }
  }

  const StatusSubtitle = () => {
    switch (status) {
      case 'submit':
        return 'Provide details regarding event';
      case 'vote':
        return 'Vote on Events to decide the outcome';
      case 'vote-event':
        return 'Vote on Events to decide the outcome';
      case 'approved':
        return 'Claim approved submissions';
      case 'approve-event':
        return 'Submitting a claim to receive payout';
      case 'assessor':
        return 'Assess Loss Events and Cost Share Request';
      case 'assessor-csrview':
        return 'Vote on Events to decide the outcome';
      default:
        return 'Assets claims and monitor claim status'
    }
  }

  return (
    <div className="Events">
      <div className="header">
        <div className="d-flex flex-column">
          <p className="fs-31 mb-10 title-color">
            {StatusTitle()}
          </p>
          <p>
            {StatusSubtitle()}
          </p>
        </div>
        <div className="d-flex flex-row">
          <button
            className={`border-05 radius-10 bg-card assessor-btn ${status === "assessor" ? "active" : ""
              }`}
            onClick={() => setStatus("assessor")}
          >
            Assessor View
          </button>
          <Select
            options={[
              {
                label: "All",
                value: "all",
              },
              {
                label: "Vote",
                value: "vote",
              },
              {
                label: "Approved",
                value: "approved",
              },
              {
                label: "Review",
                value: "review",
              },
              {
                label: "Submit",
                value: "submit",
              },
            ]}
            onChange={handleSelect}
          />
        </div>
      </div>

      {status === "assessor" && (
        <AssessorTable tableData={table_assessor_data} callback={handleTableCallback} />
      )}
      <div
        className={`fairside-table table-responsive border-05 radius-10 ${status === "assessor" ? "p-0" : "p-3"
          }`}
      >
        {(status === "all" || status === "review") && (
          <AllTable tableData={table_data} callback={handleTableCallback} />
        )}
        {status === "submit" && (
          <SubmitPage />
        )}

        {status === "vote" && (
          <VoteTable tableData={table_vote_data} callback={handleTableCallback} />
        )}

        {status === "vote-event" && (
          <VoteEventTable fromStatus={fromStatus} data={detailItem} callback={handleTableCallback} />
        )}
        {status === "approved" && (
          <ApprovedTable tableData={table_data} callback={handleTableCallback} />
        )}
        {status === "approve-event" && (
          <ApprovedEventTable data={detailItem} callback={handleTableCallback} />
        )}

        {status === "assessor-csrview" && (
          <AssessorCSRView fromStatus={fromStatus} data={detailItem} callback={handleTableCallback} />
        )}
        {status === "assessor-lossview" && (
          <AssessorLossView fromStatus={fromStatus} data={detailItem} callback={handleTableCallback} />
        )}
      </div>
      <Modal isOpen={isOpen} setIsOpen={setIsOpen}>
        <div className="d-flex align-items-center flex-column p-20">
          <img src={Confirm} alt="" className="mb-30" />
          <p className="text-color mb-0">Your vote has been submitted.</p>
        </div>
      </Modal>
    </div>
  );
};

export default Events;
