import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { rpc, RPC_OPERATIONS } from '../utils'
import useRefresh from './useRefresh'
import { WHITELIST_APP_URL } from '../config'

export const useAdvancedData = (isWalletConnected, web3, account, fsd) =>{
  const [ethBalance, setEthBalance] = useState('0')
  const [fsdBalance, setFsdBalance] = useState('0')
  const [signature, setSignature] = useState('')

  const {fastRefresh} = useRefresh()

  useEffect(() => {
    async function fetchData() {
      const { fromWei, toBN: big, toWei } = web3.utils;
      const ethBalance = await web3.eth.getBalance(account);
      const fsdBalance = await rpc(fsd, 'balanceOf', [account], RPC_OPERATIONS.CALL);
      let signature;
      try {
        ({ data: { data: { signature } } } =
          await axios.get(`${WHITELIST_APP_URL}/signature/${account}`));
      } catch (e) { console.log(e.message); }
      setEthBalance(parseFloat(fromWei(ethBalance, "ether")).toFixed(4));
      setFsdBalance(parseFloat(fromWei(fsdBalance, "ether")).toFixed(4));
      setSignature(signature)
    }  
    if(isWalletConnected) fetchData()
  }, [isWalletConnected, web3, account,fsd, fastRefresh])

  return {
    ethBalance,
    fsdBalance,
    signature,
  }
  
}