import { useState } from "react"

import LeftArrow from "../../assets/imgs/ArrowLeftIcon.svg";

export function ApprovedEventTable({ data, callback }) {
  const [lossAmount, setLossAmount] = useState("");
  const [claimAmount, setClaimAmount] = useState("");
  const [claimTxId, setClaimTxId] = useState("");

  const handleCallback = (event, fromEvent, data) => {
    callback(event, fromEvent, data)
  }

  return (
    <>
      <div className="approve-event d-flex flex-column p-20">
        <div className="d-flex flex-row">
          <img
            src={LeftArrow}
            alt=""
            className="cursor-point"
            onClick={() => handleCallback('approved')}
          />
          <div className="d-flex justify-content-center w-100 fs-18 title-color semi-bold">
            Making a claim for:
          </div>
        </div>
        <div className="mb-3"></div>
        <p className="fs-18 text-center primary-color bold cursor-point" onClick={()=>handleCallback('vote-event', 'approve-event', data)}>
          {data?.project}
        </p>
        <div className="mb-3"></div>
        <div className="d-flex justify-content-center align-items-center">
          <p className="fs-16 text-center w-50">
            Unusual activity has been reported regarding the distribution of COMP following the execution of Proposal 062.
            No supplied/borrowed funds are at risk -- Compound Labs and members of the community are investigating discrepancies in the COMP distribution.
            Select a address to claim
          </p>
        </div>
        <div className="mb-4"></div>
        <div className="mb-4"></div>

        <p className="fs-18 mb-30 text-center bold">
          Select a address to claim
        </p>
        <div className="d-flex justify-content-center mb-30">
          <div className="f-card-body mr-10">
            0x3c16183c1C0E28F1a0cB9F8ee4b21D0Db208cA46
          </div>
          <div className="f-card-body mr-10">
            0xe7E6c88Ad1BAb6508a251B7995f44fB1C5E3dCF7
          </div>
          <div className="f-card-body">
            0x1aD91ee08f21bE3dE0BA2ba6918E714dA6B45836
          </div>
        </div>
        <div className="mb-4"></div>

        <p className="fs-18 text-center bold">
          How much ETH did you lose to this exploit?
        </p>
        <div className="d-flex justify-content-center">
          <div className="f-card-button fs-16">
            31.5
          </div>
        </div>
        <div className="mb-4"></div>
        <div className="mb-4"></div>
        <p className="fs-18 text-center bold">
          How much would you like to claim?
        </p>
        <div className="d-flex justify-content-center ">
          <div className="f-card-button fs-16">
            10.5
          </div>
        </div>
        <div className="mb-4"></div>
        <div className="mb-4"></div>

        <p className="fs-18 text-center bold">
          Please enter in the transaction ID of the time of exploit
        </p>
        <p className="fs-18 text-center">
          etherscan.io/address/0xea674fdde714fd979de3edf0f56aa9716b898ec8
        </p>
        {/* <div className="row">
          <div className="col-md">
            <p className="fs-16 mb-10">Loss Amount</p>
            <input
              value={lossAmount}
              onChange={(e) => setLossAmount(e.target.value)}
            ></input>
          </div>
          <div className="col-md">
            <p className="fs-16 mb-10">Claim Amount</p>
            <input
              value={claimAmount}
              onChange={(e) => setClaimAmount(e.target.value)}
            ></input>
          </div>
          <div className="col-md">
            <p className="fs-16 mb-10">Transaction ID</p>
            <input
              value={claimTxId}
              onChange={(e) => setClaimTxId(e.target.value)}
            ></input>
          </div>
        </div> */}
        <div className="mb-4"></div>
        <div className="mb-4"></div>
        <div className="text-center align-items-center">
          <p className='fs-18 text-center bold'>Proof of Loss Requirements</p>
          <div className="d-flex justify-content-center align-items-center">
            <p className="fs-16 text-center w-50">
              Provide time stamps, links and transaction ID for every transaction made following up to exploit. Please submit all evidence and transactions during.</p>
          </div>
          <textarea
            className="input-box textarea"
            placeholder="Provide additional info here"
          />
        </div>
        <div className="text-center mt-30">
          <button className="submit">Submit</button>
        </div>
      </div>
    </>
  )
}