import { useState } from "react"

import SortDown from "../../assets/imgs/ArrowDropDownIcon.svg";
import SortUp from "../../assets/imgs/ArrowDropUpIcon.svg";
import ThreeDots from "../../assets/imgs/ThreeDots.svg";
import Approve from "../../assets/imgs/Approve.svg";
import Deny from "../../assets/imgs/Deny.svg";

export function VoteTable({tableData, callback}) {
  const [isVoteStatusSort, setIsVoteStatusSort] = useState(true);
  const [isVoteTimeSort, setIsVoteTimeSort] = useState(true);

  const handleCallback = (event, fromEvent, data) => {
    callback(event, fromEvent, data)
  }

  return (
    <table className="table table-hover">
    <thead>
      <tr>
        <td>Project</td>
        <td
          onClick={() => setIsVoteStatusSort(!isVoteStatusSort)}
          className="cursor-point"
        >
          Action{" "}
          <img
            src={isVoteStatusSort ? SortDown : SortUp}
            alt=""
            className="ml-5"
          />
        </td>
        <td
          onClick={() => setIsVoteTimeSort(!isVoteTimeSort)}
          className="cursor-point"
        >
          Remaining Time{" "}
          <img
            src={isVoteTimeSort ? SortDown : SortUp}
            alt=""
            className="ml-5"
          />
        </td>
      </tr>
    </thead>
    <tbody>
      {tableData.map((t, index) => (
        <tr key={index}>
          <td style={{ maxWidth: "900px" }}>
            <div className="d-flex flex-row">
              <img src={t.logo} alt="" className="mr-10" />
              <div className="d-flex flex-column">
                <p className="fs-16 mb-0 title-color">{t.project}</p>
                <p className="fs-13 mb-0">{t.description}</p>
              </div>
            </div>
          </td>
          <td className="align-middle">
            <p
              className="pri-color mb-0 cursor-point"
              onClick={() => handleCallback("vote-event", 'vote', t)}
            >
              View
            </p>
          </td>
          <td className="align-middle">18:26</td>
        </tr>
      ))}
    </tbody>
  </table>
  )
}