import { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import axios from 'axios';
import {toast } from 'react-toastify';

import Modal from '../../components/Modal'
import Loader from '../../assets/imgs/loader.gif'
import '../../assets/scss/Stake.scss'
import Angle_down from '../../assets/imgs/Angle-down.svg'
import Search_Icon from '../../assets/imgs/Search.svg'
import ETH_Logo from '../../assets/imgs/tokens/ETH.svg'
import Inch from '../../assets/imgs/tokens/1inch.svg'
import FairsideBG from '../../assets/imgs/Fairside-bg.svg'
import { useBasicData } from '../../hooks/useBasicData'
import { useAdvancedData } from '../../hooks/useAdvancedData';
import ERC20_ABI from "../../config/abis/ERC20.js";
import { RPC_OPERATIONS, rpc } from "../../utils";
import { useERC20Contract, useFsdContract, useFsdNetworkContract, useMinterContract, useZapContract } from '../../utils/contractHelpers';
import { stakePhases, funcsByStakingPhases, gasUsageByFunc, TokenData } from '../../config/variables';

const THREE_ETHER = "3000000000000000000";
const SignUpModalStyle = {
  padding: '',
  background: 'linear-gradient(90deg, #4569F5 0%, #4A6DF5 3%, #6885F7 29%, #7E97F8 54%, #8BA1F9 78%, #8FA5F9 100%)',
  border: '',
}

export function Staking() {
  const { isWalletConnected, account, web3 } = useSelector((state) => state.user);
  const fsd = useFsdContract();
  const zap = useZapContract();
  const minter = useMinterContract()
  const { currentStakePhase } = useBasicData()
  const { ethBalance, fsdBalance, signature } = useAdvancedData(isWalletConnected, web3, account, fsd)

  console.info('is wallet connected', isWalletConnected, account)
  const [searchKey, setSearchKey] = useState('')
  const [isOpen, setIsOpen] = useState(false)
  const [isStakingStatusOpen, setIsStakingStatusOpen] = useState(false)
  const [selectedToken, setSelectedToken] = useState(TokenData[0])

  const [tokensToMint, setTokensToMint] = useState('0')
  const [axiosReq, setAxiosReq] = useState(false)
  const [inputValue, setInputValue] = useState('')
  const [tokenAddress, setTokenAddress] = useState(TokenData[0].address)
  const [decimals, setDecimals] = useState(0)
  const [tokenInstance, setTokenInstance] = useState(null)
  const [allowance, setAllowance] = useState()
  const [transactionCost, setTransactionCost] = useState('0')
  const [votingPower, setVotingPower] = useState()
  const [txHash, setTxHash] = useState()

  const [isPending, setIsPending] = useState(false)
  const [isApprovePending, setIsApprovePending] = useState(false)

  useEffect(() => {
    async function fetchAmount() {
      for (let i = 0; i < TokenData.length; i++) {
        if (i === 0) {
          TokenData[i].balanceOf = ethBalance
          continue;
        }
        else {
          const erc20 = new web3.eth.Contract(ERC20_ABI, TokenData[i].address)
          const balanceOf = await rpc(erc20, 'balanceOf', [account], RPC_OPERATIONS.CALL)
          const allowance = await rpc(erc20, 'allowance', [account, zap._address], RPC_OPERATIONS.CALL, { from: account })
          TokenData[i].balanceOf = (balanceOf / 10 ** TokenData[i].decimals).toFixed(4);
          TokenData[i].allowance = (allowance / 10 ** TokenData[i].decimals).toFixed(4);
          if (selectedToken === TokenData[i].address) {
            setAllowance((allowance / 10 ** TokenData[i].decimals).toFixed(4))
          }
        }
      }
    }
    if (isWalletConnected) {
      fetchAmount()
    }
  }, [isWalletConnected, account, ethBalance, web3, zap._address])
  

  const handleSelectToken = (token) => {
    if (!isWalletConnected)
      return warning("Wallet not connected");

    console.info('selected token', TokenData[token.id])

    const isconnect = TokenData[token.id].address !== TokenData[0].address;
    if (isconnect) {
      const tokenInstance = new web3.eth.Contract(ERC20_ABI, TokenData[token.id].address);
      setTokenInstance(tokenInstance)
      setAllowance(TokenData[token.id].allowance)
      setDecimals(TokenData[token.id].decimals)
      setInputValue('')
      setTokensToMint('0')
    }

    setTokenAddress(TokenData[token.id].address)
    setSelectedToken(token)
    setIsOpen(false)
  }

  const onChangeInputValue = async ({ target: { value } }) => {
    const currentPhase = stakePhases[currentStakePhase];
    if (!isWalletConnected)
      return warning("Wallet not connected");

    if (!!axiosReq) {
      await axiosReq.cancel();
      setAxiosReq(null)
    }

    const { toBN: big, toWei, fromWei } = web3.utils;

    if (!signature && (currentPhase === "VCWL" || currentPhase === "CWL"))
      return warning("You are not whitelisted");

    if (!value) {
      setTokensToMint('0')
      return setInputValue(value);
    }

    if (isNaN(parseFloat(value)))
      return warning("Please provide valid value");
    let convertedEth;
    if (tokenAddress !== TokenData[0].address) {
      const erc20Address = tokenAddress;

      const valueWithDecimals = big(toWei(value, "ether")).div(big(10).pow(big(18 - decimals * 1)))


      const ourRequest = axios.CancelToken.source();
      setInputValue(value)
      setAxiosReq(ourRequest)

      let response;

      try {
        response = await axios.get(
          "https://api.1inch.io/v4.0/1/swap?" +
          "fromTokenAddress=" + erc20Address +
          "&toTokenAddress=0xEeeeeEeeeEeEeeEeEeEeeEEEeeeeEeeeeeeeEEeE" +
          "&amount=" + valueWithDecimals.toString() +
          "&fromAddress=" + account +
          "&slippage=1" +
          "&disableEstimate=true",
          {
            cancelToken: ourRequest.token
          }
        );
        setAxiosReq(null)
        const {
          data: {
            tx: {
              data,
              to
            },
            toTokenAmount: ethReceived,
          }
        } = response;
        convertedEth = ethReceived
        const estimateGas = big(gasUsageByFunc["swapFromToken"]);
        const gasCost = estimateGas.mul(big(await web3.eth.getGasPrice()));
        setTransactionCost(parseFloat(fromWei(gasCost, "ether")).toFixed(6))
      } catch (err) {
        console.log(err)
        convertedEth = '0'
      }
    }

    let inputInWei;
    let v;
    if (tokenAddress !== TokenData[0].address) {
      inputInWei = convertedEth;
    } else {
      v = value;
      try { inputInWei = big(toWei(v, "ether")); }
      catch (e) { console.info('errorrrrr', e); return warning(e); }
    }
    setInputValue(value);

    const tokensToMint = big(
      await rpc(
        fsd,
        "getTokensMinted",
        [inputInWei],
        RPC_OPERATIONS.CALL
      )
    );

    const totalSupply = big(
      await rpc(
        fsd,
        "totalSupply",
        [],
        RPC_OPERATIONS.CALL
      )
    );

    setTokensToMint(fromWei(tokensToMint, "ether"));
    setVotingPower(parseFloat(fromWei(
      tokensToMint
        .mul(big(10).pow(big(18)))
        .div(tokensToMint.add(totalSupply)),
      "ether"
    )).toFixed(4));

    let estimateGas;
    const opts = { from: account, value: inputInWei };

    switch (currentPhase) {
      case "VCWL":
      case "CWL": {
        estimateGas = big(gasUsageByFunc[funcsByStakingPhases[currentPhase]]);

        // 	big(
        // 	await rpc(
        // 		minter,
        // 		funcsByStakingPhases[currentPhase],
        // 		[signature, tokensToMint],
        // 		RPC_OPERATIONS.ESTIMATE,
        // 		opts
        // 	)
        // );
        break;
      }
      case "Final": {
        estimateGas = big(gasUsageByFunc["mint"]);

        // 	big(
        // 	await rpc(
        // 		minter,
        // 		"mint",
        // 		[tokensToMint],
        // 		RPC_OPERATIONS.ESTIMATE,
        // 		opts
        // 	)
        // );
        break;
      }
      default: {
        return warning("Invalid staking phase");
      }
    }
    const gasPrice = big(await web3.eth.getGasPrice());
    const gasCost = estimateGas.mul(gasPrice);
    // setTransactionCost(parseFloat(fromWei(inputInWei.add(gasCost), "ether")).toFixed(4));
  }

  const performApprovalFunc = async () => {
    if (!isWalletConnected)
      return warning("Wallet not connected");

    const { toBN: big, toWei } = web3.utils;

    if (isNaN(parseFloat(inputValue)) || inputValue <= 0)
      return warning("Please provide correct valid value");

    const erc20Contract = tokenInstance;

    const valueWithDecimals = big(inputValue).mul(big(10).pow(big(decimals)));
    setIsApprovePending(true)
    try {
      const { transactionHash: txHash } = await rpc(
        erc20Contract,
        "approve",
        [zap._address, valueWithDecimals],
        RPC_OPERATIONS.SEND,
        { from: account }
      );
      setIsApprovePending(false)
    } catch (err) {
      setIsApprovePending(false)
      console.error(err)
    }
    setTxHash(txHash)
    warning('You have successfully performed approval')
  }

  const handleSubmit = async () => {
    const isEthStaking = tokenAddress === TokenData[0].address;

    if (isEthStaking)
      await handleSubmitStake();
    else
      await stakeWithERC20();
  }

  const handleSubmitStake = async () => {
    if (!isWalletConnected)
      return warning("Wallet not connected");

    const { toWei, toBN: big } = web3.utils;
    const currentPhase = stakePhases[currentStakePhase];

    if (!signature && (currentPhase === "VCWL" || currentPhase === "CWL"))
      return warning("You are not whitelisted");

    if (isNaN(parseFloat(inputValue)) || inputValue <= 0)
      return warning("Please provide correct valid value");

    setIsPending(true)

    let inputInWei;
    try { inputInWei = big(toWei(inputValue, "ether")); }
    catch (e) { setIsPending(false); return warning(e.message()); }

    if (inputInWei.lt(big(THREE_ETHER)) && currentPhase === "VCWL") {
      setIsPending(false)
      return warning("Please make sure the stake amount satisfies the phase");
    }

    let txHash;
    const opts = { from: account, value: inputInWei };
    switch (currentPhase) {
      case "VCWL":
      case "CWL": {
        try {
          ({ transactionHash: txHash } = await rpc(
            minter,
            funcsByStakingPhases[currentPhase],
            [signature, toWei(tokensToMint, "ether")],
            RPC_OPERATIONS.SEND,
            opts
          ));
        } catch {
          setIsPending(false)
        }
        break;
      }
      case "Final": {
        ({ transactionHash: txHash } = await rpc(
          minter,
          "mint",
          [toWei(tokensToMint, "ether")],
          RPC_OPERATIONS.SEND,
          opts
        ));
        break;
      }
      default: {
        setIsPending(false)
        return warning("Invalid staking phase");
      }
    }
    setIsPending(false)
    if (txHash)
      setIsStakingStatusOpen(true)
    setTxHash(txHash)
  }

  const stakeWithERC20 = async () => {
    if (!isWalletConnected)
      return warning("Wallet not connected");

    const { toBN: big } = web3.utils;

    if (isNaN(parseFloat(inputValue)) || inputValue <= 0)
      return warning("Please provide correct valid value");

    const erc20Address = tokenAddress;
    const erc20Contract = tokenInstance;

    const valueWithDecimals = big(inputValue).mul(big(10).pow(big(decimals)));

    const allowance = big(
      await rpc(
        erc20Contract,
        "allowance",
        [account, zap._address],
        RPC_OPERATIONS.CALL
      )
    );

    if (allowance.lt(valueWithDecimals)) {
      return warning("Insufficient allowance");
    }

    const response = await axios.get(
      "https://api.1inch.io/v4.0/1/swap?" +
      "fromTokenAddress=" + erc20Address +
      "&toTokenAddress=0xEeeeeEeeeEeEeeEeEeEeeEEEeeeeEeeeeeeeEEeE" +
      "&amount=" + valueWithDecimals.toString() +
      "&fromAddress=" + account +
      "&slippage=1" +
      "&disableEstimate=true"
    );

    const {
      data: {
        tx: {
          data,
          to
        }
      }
    } = response;

    console.info("swapToken", erc20Address, valueWithDecimals, 1, to, data)

    const { transactionHash: txHash } = await rpc(
      zap,
      "swapFromToken",
      [erc20Address, valueWithDecimals, 1, to, data],
      RPC_OPERATIONS.SEND,
      { from: account }
    );

    console.log("TxHash:", txHash);

    // this.setState({
    // 	notification: true,
    // 	txHash,
    // 	msg: "You have staked your ERC20 tokens successfully."
    // });
    // setNotification(true)
    setTxHash(txHash)
    warning('You have staked your ERC20 tokens successfully')
  }

  const filterTokenDataFunc = () => {
    const valueToLowerCase = searchKey.toLowerCase()
    const filterdTokenData = TokenData.filter(t => { return t.address.toLowerCase().search(valueToLowerCase) > -1 || t.symbol.toLowerCase().search(valueToLowerCase) > -1 || t.name.toLowerCase().search(valueToLowerCase) > -1 });
    return filterdTokenData
  }

  function warning(str) {
    return toast.warning(<div>&nbsp;&nbsp;&nbsp;{str}</div>)
  }

  return (
    <>
      <div className='col-lg mt-md-3 mt-lg-0'>
        <div className='inch-card'>
          <div className='mt-lg-3 mt-md-0'>
            <p className='sec-dark-color fs-21'>
              {'Stake ETH or ERC-20 Token:'}
            </p>
            <div className='inch-select' onClick={() => setIsOpen(true)}>
              <img src={selectedToken.logo} height='20px' alt='' />
              {
                selectedToken.symbol
              }
              <img src={Angle_down} alt='angle-down-icon' />
            </div>

          </div>
          <div>
            <div className='d-flex flex-row justify-content-between inch-input-description'>
              <p>
                {`Amount of ${selectedToken.name} to stake`}
              </p>
              <p onClick={() => setInputValue(selectedToken.balanceOf)}>{`Available: ${isWalletConnected ? selectedToken.balanceOf : ' -'}`}</p>
            </div>
            <input type='number' value={inputValue} onChange={(e) => onChangeInputValue(e)} min={0} className='input-box' />
          </div>
          <div className='border-05 radius-10 p-10 d-flex flex-column mt-10'>
            <div className='d-flex justify-content-between'>
              <p className='mb-5'>Slippage Fee</p>
              <p className='mb-5 pri-color'>0.0034 ETH</p>
            </div>
            <div className='d-flex justify-content-between'>
              <p className='mb-5'>Amount of FSD received</p>
              <p className='mb-5 pri-color'>{parseFloat(tokensToMint).toFixed(4)} FSD</p>
            </div>
            <div className='d-flex justify-content-between'>
              <p className='mb-5'>Conviction Score Gained</p>
              <p className='mb-5 pri-color'>0.145</p>
            </div>
            {
              selectedToken.symbol !== 'ETH' && <>
                <div>
                  <p className='mb-10'>Router ERC-20:</p>
                  <div className='d-flex flex-row justify-content-between router'>
                    <button><img src={selectedToken.logo} height='18px' alt='' />100%</button>
                    <div className='d-flex'></div>
                    <button><img src={Inch} alt='' height='20px' />0.05%</button>
                    <div className='d-flex'></div>
                    <button><img src={ETH_Logo} alt='' />100%</button>
                  </div>
                </div>
              </>
            }
          </div>
          <p className='inch-description mt-10'>
            {'Your selected ERC-20 tokens will be routed through 1inch and zapped for staking on Fairside. Best price routes costs ~$15.61 for fees. This route optimizes your input considering splits routes, hops and  gas cost of each step.'
            }
          </p>
          <div className='inch-buttons'>
            {
              <>
                {selectedToken.symbol !== 'ETH' && <button className={`button-hover-shadow ${parseFloat(allowance) < parseFloat(inputValue) ? 'approved' : 'disabled'}`} onClick={() => performApprovalFunc()}>{isApprovePending ? <> Approving <img src={Loader} alt='loading...' height='20px' /></> : 'Approve'}</button>}
                <button className={`button-hover-shadow ${selectedToken.symbol === 'ETH' ? 'approved' : parseFloat(allowance) >= parseFloat(inputValue) ? 'approved' : 'disabled'}`} onClick={() => handleSubmit()}>{isPending ? <> Staking <img src={Loader} alt='loading...' height='20px' className='ml-10' /></> : 'Stake'}</button>
              </>
            }
          </div>
        </div>
      </div>
      <Modal isOpen={isOpen} setIsOpen={setIsOpen}>
        <div className='search-modal'>
          <p className='title'>Select a Token</p>
          <div className="search-input">
            <img src={Search_Icon} alt='search-icon' />
            <input type="text" placeholder='Search by name or paste address' value={searchKey} onChange={(e) => setSearchKey(e.target.value)} />
          </div>
          <div className='token-list-wrapper'>
            {
              filterTokenDataFunc().map((token, index) => {
                if (token.name === 'FSD') return ''
                if (token.name.toLowerCase().indexOf(searchKey.toLowerCase()) < 0) return ''
                return (
                  <div key={index} className='token-list' onClick={() => handleSelectToken(token)}>
                    <div className='token-name-wrapper'>
                      <img src={token.logo} alt='' height='40px' />
                      <div className='token-name'>
                        <p>{token.symbol}</p>
                        <p>{token.name}</p>
                      </div>
                    </div>
                    <div className='token-value'>
                      {isWalletConnected ? token.balanceOf : 0}
                    </div>
                  </div>
                )
              })
            }
          </div>
        </div>
      </Modal>

      <Modal isOpen={isStakingStatusOpen} setIsOpen={setIsStakingStatusOpen} content_style={SignUpModalStyle}>
        <div className='join-alert'>
          <img src={FairsideBG} alt='' />
          <h4>Thank you for staking Fairside</h4>
          <p>Transaction ID: <a href={`https://etherscan.io/tx/${txHash}`} target='_blank' rel='noreferrer'>https://etherscan.io/tx</a></p>
        </div>
      </Modal>
    </>
  )
}