import React from 'react'
import ReactModal from 'react-modal'

const ModalStyles = {
  overlay: {
    // backgroundColor: 'rgba(255, 255, 255, 0.75)'
    backgroundColor: 'rgba(0, 0, 0, 0.3)'
  },
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    borderRadius: '10px',
    border: '0.5px solid #E1E1E1',
    padding: '20px'
  },
}

ReactModal.setAppElement('#root')

const Modal = ({ children, ...props }) => {
  const { isOpen, setIsOpen } = props
  const content_style = props.content_style ? props.content_style : {}

  // object deep copy
  const customModalStyle = JSON.parse(JSON.stringify(ModalStyles));

  Object.assign(customModalStyle.content, content_style)

  return (
    <ReactModal isOpen={isOpen} style={customModalStyle} onRequestClose={() => setIsOpen(false)}>
      {children}
    </ReactModal>
  )
}


export default Modal