import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import Web3 from 'web3';
import Onboard from "@web3-onboard/core";
import { useSelector } from 'react-redux';
import { useAppDispatch } from '../state';

import Modal from './Modal';

import { toHex, truncateAddress, deBankUserTokensListApi } from '../utils';
import { Onboard_Option } from '../config/wallet.config';

import { getConnected, getDisconnected } from '../state/user';
import Loader from '../assets/imgs/loader.gif'
import Logo from '../assets/imgs/Logo.svg';
import Alert from '../assets/imgs/Alert.svg'
import ExternalLinkIcon from '../assets/imgs/ExternalLinkIcon.svg'

import '../assets/scss/Header.scss'

const networkId = process.env.REACT_APP_NETWORK_ID

console.log('networkId', networkId)

const onboard = Onboard(Onboard_Option)


const Header = () => {

  const [isOpen, setIsOpen] = useState(false)
  const [isConnecting, setIsConnecting] = useState(false)
  const dispatch = useAppDispatch();
  const { account } = useSelector((state) => state.user)

  const login = async () => {
    console.info('isconnnecting-------', isConnecting)
    console.log('loginggggg', onboard)
    setIsConnecting(true)
    try {
      const previewConnectedWalletType = window.localStorage.getItem('previousConnectedWalletType')
      const option = previewConnectedWalletType ? {
        autoSelect: previewConnectedWalletType
      } : {};
      const wallets = await onboard.connectWallet(option);
      const { label, accounts, chains, provider } = wallets[0];
      const { id } = chains[0]

      if (id !== toHex(networkId)) {
        const result = await onboard.setChain({ chainId: toHex(networkId) });
        console.log('result', result)
        if (!result) {
          logout()
          return
        }
      }
      let web3 = new Web3(provider);
      const { address } = accounts[0]
      const result = await deBankUserTokensListApi(address)
      console.info('debank', result)
      dispatch(getConnected({
        web3,
        account: address
      }))
      setIsConnecting(false)
      window.localStorage.setItem("previousConnectedWalletType", label)
    } catch (error) {
      setIsConnecting(false)
      // setError(error);
      dispatch(getDisconnected())
    }
  };

  const logout = async () => {
    const [primaryWallet] = await onboard.state.get().wallets;
    if (!primaryWallet) return;
    await onboard.disconnectWallet({ label: primaryWallet.label });
    setIsOpen(false)
    dispatch(getDisconnected())
  };

  const wallet = () => {
    if (account) {
      // logout()
      setIsOpen(true)
    }
    else {
      login()
    }
  }

  useEffect(() => {
    if (window.localStorage.getItem('previousConnectedWalletType') && !account) {
      login()
    }
  }, [account]);

  return (
    <div className='header d-flex justify-content-between'>
      <Link to='/' className='logo-wrapper'>
        <img src={Logo} alt="Fairside" />
      </Link>
      <div className='d-flex flex-row'>
        <div className='alert-wrapper'>
          <img src={Alert} alt='alert' width={'20px'} height={'20px'} />
        </div>
        <button className='connect-button' onClick={() => wallet()}>
          {
            account ? truncateAddress(account) : isConnecting ? <img src={Loader} alt='loading...' height='20px' /> : 'Connect'
          }
        </button>
      </div>
      <Modal isOpen={isOpen} setIsOpen={setIsOpen}>
        <div className='wallet p-20'>
          <p className='text-center mb-30 fs-26 title-color semi-bold'>Your Wallet</p>
          <div className='address active'>
            0x1aD91ee08f......14dA6B45836
          </div>
          <div className='address'>
            0x1aD91ee08f......14dA6B45836
          </div>
          <div className='address'>
            0x1aD91ee08f......14dA6B45836
          </div>
          <div className='d-flex justify-content-between fs-15 mt-10'>
            <p>Eth Balance</p>
            13.1
          </div>
          <div className='d-flex justify-content-between fs-15'>
            <p>FSD Balance</p>
            89,315.15
          </div>
          <div className='d-flex justify-content-end'>
            <a href='https://etherscan.io/' target='_blank' rel='noreferrer'>
              <p className='pri-color fs-15'>View on EtherScan <img src={ExternalLinkIcon} alt='' /></p>
            </a>
          </div>
          <div className='text-center'>
            <button onClick={() => logout()} className='submit'>Disconnect Wallet</button>
          </div>
        </div>

      </Modal>
    </div>
  )
}

export default Header