import { createSlice } from "@reduxjs/toolkit";
import { WEB3_RPC } from "../config";


const initialState = { 
  isWalletConnected: false,
  web3: WEB3_RPC,
  account: '',
}

export const UserSlice = createSlice({
  name: 'user',
  initialState,
  reducers:{
    getConnected: (state, action) => {
      const { web3, account} = action.payload
      console.log('web3, account', web3, account)
      state.isWalletConnected = true;
      state.web3 = web3;
      state.account = account;
    },
    getDisconnected: (state)=> {
      state.isWalletConnected = false;
      state.web3 = WEB3_RPC;
      state.account = '';
      window.localStorage.removeItem('previousConnectedWalletType')
    }
  }
})

export const {
  getConnected,
  getDisconnected
} = UserSlice.actions

export default UserSlice.reducer