import { useState } from "react"

import Modal from "../Modal";

import LeftArrow from "../../assets/imgs/ArrowLeftIcon.svg";
import ExternalLink from "../../assets/imgs/ExternalLinkIcon.svg";
import Confirm from "../../assets/imgs/ConfirmIcon.svg";

export function AssessorLossView({ fromStatus, data, callback }) {
  const [isAgainst, setIsAgainst] = useState(false);
  const [isOpen, setIsOpen] = useState(false);

  const handleCallback = (event, fromEvent) => {
    callback(event, fromEvent)
  }

  return (
    <>
      <div className="p-20">
        <div className="d-flex flex-row mb-30">
          <img
            src={LeftArrow}
            alt=""
            className="cursor-point"
            onClick={() => handleCallback(fromStatus, 'all')}
          />
          <div className="d-flex justify-content-center w-100 fs-26 title-color semi-bold">
            {data?.request}
          </div>
        </div>
        <div className="row">
          <div className="f-card-body mr-30">
            <p className="fs-21 title-color semi-bold mb-30">Details</p>
            <div className="d-flex flex-row">
              <p className="fs-16 semi-bold title-color mr-10">
                Affected Address:
              </p>
              <p>
                0x3c16183c1C0E28F1a0cB9F8ee4b21D0Db208cA46{" "}
                <a
                  rel="noreferrer"
                  href="https://etherscan.io/address/0x3c16183c1C0E28F1a0cB9F8ee4b21D0Db208cA46"
                  target="_blank"
                >
                  <img src={ExternalLink} alt="" className="ml-10" />
                </a>
              </p>
            </div>
            <div className="d-flex flex-row">
              <div className="d-flex flex-row mr-30">
                <p className="fs-16 semi-bold title-color mr-10">
                  Asset Lost
                </p>
                <p>20 ETH</p>
              </div>
              <div className="d-flex flex-row">
                <p className="fs-16 semi-bold title-color mr-10">
                  Coverage
                </p>
                <p>10 ETH</p>
              </div>
            </div>
            <div className="mb-30">
              Unusual activity has been reported regarding the
              distribution of COMP following the execution of Proposal
              062. No supplied/borrowed funds are at risk -- Compound Labs
              and members of the community are investigating discrepancies
              in the COMP distribution.
            </div>
            <p className="fs-21 title-color semi-bold mb-10">
              Requirements
            </p>
            <div className="mb-30">
              Provide time stamps, links and transaction ID for every
              transaction made following up to exploit.
            </div>

            <hr/>
            <div className="d-flex flex-row justify-content-center gap-5 mb-4">
              <div className="d-flex flex-column align-items-center">
                <p>For Proposal</p>
                <div
                  className={`cursor-point option ${!isAgainst ? "active" : ""
                    }`}
                  onClick={() => setIsAgainst(!isAgainst)}
                />
              </div>
              <div className="d-flex flex-column align-items-center">
                <p>Against Proposal</p>
                <div
                  className={`cursor-point option ${isAgainst ? "active" : ""
                    }`}
                  onClick={() => setIsAgainst(!isAgainst)}
                />
              </div>
            </div>
            <div className="d-flex justify-content-center">
              <button className="submit" onClick={() => setIsOpen(true)}>
                Submit
              </button>
            </div>
          </div>

        </div>
      </div>
      <Modal isOpen={isOpen} setIsOpen={setIsOpen}>
        <div className="d-flex align-items-center flex-column p-20">
          <img src={Confirm} alt="" className="mb-30" />
          <p className="text-color mb-0">Your vote has been submitted.</p>
        </div>
      </Modal>
    </>
  )
}