import { useMemo } from "react";
import { useSelector } from "react-redux";

// abi files

import FSD_ABI from "../config/abis/FSD";
import FSD_NETWORK_ABI from "../config/abis/FSD_NETWORK";
import ZAP_ABI from "../config/abis/ZAP";
import MINTER_ABI from "../config/abis/MINTER";
import ERC20_ABI from "../config/abis/ERC20";
import { add } from "react-modal/lib/helpers/classList";

import {
  FSD_ADDRESS,
  FSD_NETWORK_ADDRESS,
  ZAP_ADDRESS,
  MINTER_ADDRESS,
} from "../config";

export const addresses = {
  fsd: FSD_ADDRESS,
  fsdNetwork: FSD_NETWORK_ADDRESS,
  zap: ZAP_ADDRESS,
  minter: MINTER_ADDRESS,
};

export const getContract = (abi, address, web3) => {
  return new web3.eth.Contract(abi, address);
};

export const useERC20Contract = (address) => {
  const web3 = useSelector((state) => state.user.web3);
  return useMemo(() => getContract(ERC20_ABI, address, web3), [web3, address]);
};

export const useFsdContract = () => {
  const web3 = useSelector((state) => state.user.web3);
  return useMemo(() => getContract(FSD_ABI, addresses.fsd, web3), [web3]);
};

export const useFsdNetworkContract = () => {
  const web3 = useSelector((state) => state.user.web3);
  return useMemo(
    () => getContract(FSD_NETWORK_ABI, addresses.fsdNetwork, web3),
    [web3]
  );
};

export const useZapContract = () => {
  const web3 = useSelector((state) => state.user.web3);
  return useMemo(() => getContract(ZAP_ABI, addresses.zap, web3), [web3]);
};

export const useMinterContract = () => {
  const web3 = useSelector((state) => state.user.web3);
  return useMemo(() => getContract(MINTER_ABI, addresses.minter, web3), [web3]);
};
