import React, { useState } from 'react'

import '../assets/scss/MembershipSign.scss'
import '../assets/scss/Stake.scss'
import Member from '../assets/imgs/Member.svg'
import ETH from '../assets/imgs/tokens/ETH.svg'
import FSD from '../assets/imgs/tokens/FSD.svg'
import BNB from '../assets/imgs/tokens/BNB.svg'
import SOL from '../assets/imgs/tokens/SOL.svg'
import AVAX from '../assets/imgs/tokens/AVAX.svg'
import Polkadot from '../assets/imgs/tokens/Polkadot.svg'
import Polygon from '../assets/imgs/tokens/Polygon.svg'
import Luna from '../assets/imgs/tokens/Luna.svg'
import Cardano from '../assets/imgs/tokens/Cardano.svg'
import FairsideBG from '../assets/imgs/Fairside-bg.svg'
import Helper from '../assets/imgs/Helper.svg'

import Select from '../components/Select'
import Modal from '../components/Modal'

const SignUpModalStyle = {
  padding: '',
  background: 'linear-gradient(90deg, #4569F5 0%, #4A6DF5 3%, #6885F7 29%, #7E97F8 54%, #8BA1F9 78%, #8FA5F9 100%)',
  border: '',
}

const MembershipSign = () => {

  const [selectedAddress, setSelectedAddress] = useState(0)
  const [coverAmount, setCoverAmount] = useState()
  const [isNext, setIsNext] = useState(false)

  const [isOpen, setIsOpen] = useState(false)

  const handleSelect = (option) => {
    console.info(option.value)
  }
  return (
    <div className='Stake MembershipSign'>
      <div className='body'>
        <div className='row'>
          <div className='col-lg-4 body-card'>
            <img src={Member} alt='' />
            <h4 style={{ 'fontFamily': 'AileronSemiBold', 'color': '#5A6169', 'marginBottom': '20px' }}>Membership Sign Up</h4>
            <div className='text-wrapper title-color'>
              FairSide does not issue you a policy when you sign up for membership. There is no confusing policy language or exclusions to worry about. If the community has approved an unfair loss event, they have agreed to share in the loss. You are responsible to curate your proof of loss designated within an approved event. Upon confirmation of evidence, the claim will be paid in ETH or DAI to your wallet address via smart contract.
            </div>
            <div className='bottom'>
              <h4 className='pri-color'>Learn More</h4>
            </div>
          </div>
          <div className='col-lg mt-md-3 mt-lg-0'>
            <div className='membership-card radius-10 border-05 p-20'>
              {
                !isNext &&
                <>

                  <div className='d-flex flex-column'>
                    <p className='mb-5 title-color fs-18 semi-bold'>Select Address to Cover <img src={Helper} alt='' className='ml-5'/></p>
                    <p className='mb-30 text-color fs-14'>The selected address will be valid for coverage in events of an accident.</p>
                    {/* <div className='d-flex cover-button'>
                      <button onClick={() => setSelectedAddress(1)} className={selectedAddress === 1 ? 'active' : ''}>1</button>
                      <button onClick={() => setSelectedAddress(2)} className={selectedAddress === 2 ? 'active' : ''}>2</button>
                      <button onClick={() => setSelectedAddress(3)} className={selectedAddress === 3 ? 'active' : ''}>3</button>
                    </div> */}
                    <div>
                      <p className='mb-2'>Primary Wallet:</p>
                      <div className='address'>0x1aD91ee08f21bE3dE0BA2ba6918E714dA6B45836</div>
                    </div>
                    <div>
                      <p className='mb-2'>Secondary Wallets:</p>
                      <div className='d-flex flex-row flex-wrap'>
                        <div className='address'>0x1aD91ee08f21bE3dE0BA2ba6918E714dA6B45836</div>
                        <div className='address'>0x1aD91ee08f21bE3dE0BA2ba6918E714dA6B45836</div>
                      </div>
                    </div>
                  </div>
                  <div className='row mt-30'>
                    <div className='d-flex flex-column col-lg mr-30'>
                      <p className='fs-18 semi-bold title-color mb-10'>Coverage Amount</p>
                      <p className='fs-14 text-color mb-20'>The minimum coverage amount  needed is 1 ETH.</p>
                      <input className='input-box mt-0' type='number' value={coverAmount} onChange={(e) => setCoverAmount(e.target.value)} />
                    </div>
                    <div className='d-flex flex-column col-lg mr-30'>
                      <p className='fs-18 semi-bold title-color mb-10'>Payment Type</p>
                      <p className='fs-14 text-color mb-20'>Pay for your cover with ETH  or ERC2-0 token.</p>
                      <div className='d-flex flex-row align-items-center'>
                        <Select
                          options={[
                            {
                              label: 'ETH',
                              value: 'eth',
                              // icon: ETH,
                            },
                            {
                              label: 'FSD',
                              value: 'fsd',
                              // icon: FSD
                            }
                          ]}
                          onChange={handleSelect}
                        />
                        <p className='ml-10 mb-0'><img src={Helper} alt='' className='mr-10'/>4% Membership Fee</p>
                      </div>
                    </div>
                  </div>
                  {/* <div>
                    <p className='semi-bold fs-18 title-color'>Platforms We Cover</p>
                    <div className='d-flex flex-row flex-wrap platforms'>
                      <button>Binance Smart Chain <img src={BNB} alt='' /></button>
                      <button>Ethereum <img src={ETH} alt='' /></button>
                      <button>Solana <img src={SOL} alt='' /></button>
                      <button>AVAX <img src={AVAX} alt='' /></button>
                    </div>
                  </div>
                  <div>
                    <p className='semi-bold fs-18 title-color mt-30'>Cover Type</p>
                    <div className='d-flex flex-row flex-wrap platforms'>
                      <button>Bug Exploits</button>
                      <button>Network Crash</button>
                      <button>Rug Pulls</button>
                      <button>Phishing</button>
                    </div>
                  </div> */}
                  <div className='border-05 radius-10 p-10 d-flex flex-column'>
                    <div className='d-flex justify-content-between'>
                      <p className='mb-2'>Flat Premium</p>
                      <p className='pri-color mb-2'>4%</p>
                    </div>
                    <div className='d-flex justify-content-between'>
                      <p className='mb-2'>Fee</p>
                      <p className='mb-2 pri-color'>1.45 ETH</p>
                    </div>
                    <div className='d-flex justify-content-between'>
                      <p className='mb-0'>Slippage</p>
                      <p className='mb-0 pri-color'>0.35%</p>
                    </div>
                  </div>
                  <div className='fs-14 mt-30 mb-30'>
                    Disclaimer: Wallets chosen cannot be modified after purchasing cover. Addresses will be kept in a data base and
                    will not need verifcation through wallet interface. Please note, the amount of coverage purchased is for the specific
                    wallet only and does not include all three.
                  </div>
                  <div className='d-flex justify-content-end'>
                    <button className='submit' onClick={() => setIsNext(true)}>Next</button>
                  </div>
                </>
              }
              {
                isNext &&
                <>
                  <div className='d-flex flex-column justify-content-center h-100 p-20'>
                    <p className='fs-18 semi-bold title-color mb-10'>One last step!</p>
                    <div className='step-text-wrapper mt-10'>
                      It's super easy! Simply launch the app from the landing page and Start Membership. Connect your wallet and choose the number of ETH benefits you need. The membership fee will automatically generate and prompt you for payment. Confirm your payment below and the wallet prompt.
                    </div>
                    <div className='mt-30'>
                      <p className='semi-bold fs-18 title-color'>Platforms We Cover</p>
                      <div className='d-flex flex-row flex-wrap platforms'>
                        <button>Binance Smart Chain <img src={BNB} alt='' /></button>
                        <button>Ethereum <img src={ETH} alt='' /></button>
                        <button>Solana <img src={SOL} alt='' /></button>
                        <button>Polkadot <img src={Polkadot} alt='' /></button>
                        <button>Luna <img src={Luna} alt='' /></button>
                        <button>Polygon <img src={Polygon} alt='' /></button>
                        <button>Cardano <img src={Cardano} alt='' /></button>
                      </div>
                    </div>
                    <div className='mt-10'>
                      <p className='semi-bold fs-18 title-color'>Cover Types <img src={Helper} alt='' className='ml-5'/></p>
                      <div className='card-body border-05 radius-10'>
                        <span className='pri-color'>Smart Contract Failure </span> - If the code of the library is flawed, the code using the library may be flawed too. An anonymous person gained control over a flawed library and allegedly called the "kill" function, which destroyed the library
                      </div>
                    </div>
                    <div className='d-flex flex-column mt-30 card summary'>
                      <p className='fs-18 title-color semi-bold mb-10 lh=22'>Summary</p>
                      <div className='d-flex flex-row justify-content-between mt-10'>
                        <p className='fs-16 title-color semi-bold'>Cover amount <img src={Helper} alt='' className='ml-5'/></p>
                        <p className='fs-16'>31.5 ETH</p>
                      </div>
                      <div className='d-flex flex-row justify-content-between'>
                        <p className='fs-16 title-color semi-bold'>Payment Type <img src={Helper} alt='' className='ml-5'/></p>
                        <p>ETH</p>
                      </div>
                      <div className='d-flex flex-row justify-content-between'>
                        <p className='fs-16 title-color semi-bold'>Cover Duration <img src={Helper} alt='' className='ml-5'/></p>
                        <p>1 Year</p>
                      </div>
                      <div className='d-flex flex-row justify-content-between'>
                        <p className='fs-16 title-color semi-bold'>Membership Fee <img src={Helper} alt='' className='ml-5'/></p>
                        <p>4%</p>
                      </div>
                      <div className='d-flex flex-row justify-content-between'>
                        <p className='fs-16 title-color semi-bold'>Slippage <img src={Helper} alt='' className='ml-5'/></p>
                        <p>0.004%</p>
                      </div>
                      <div className='d-flex flex-row justify-content-between mt-10'>
                        <p className='fs-16 title-color semi-bold'>Amount Due: </p>
                        <p>1.5 ETH</p>
                      </div>
                      <button className='submit mb-10' onClick={() => setIsOpen(true)}>Purchase Cover</button>
                    </div>
                  </div>
                </>
              }
            </div>
          </div>
        </div>
      </div>
      <Modal isOpen={isOpen} setIsOpen={setIsOpen} content_style={SignUpModalStyle}>
        <div className='join-alert'>
          <img src={FairsideBG} alt='' />
          <h4>Thank you for joining Fairside</h4>
          <p>Transaction ID: <a href='https://etherscan.io/tx' target='_blank' rel='noreferrer'>https://etherscan.io/tx</a></p>
        </div>
      </Modal>
    </div >
  )
}

export default MembershipSign