import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import Chart from "react-apexcharts";
import BigNumber from "bignumber.js";

import { useBasicData } from '../hooks/useBasicData'
import { useAdvancedData } from '../hooks/useAdvancedData';
import { deBankUserTokensListApi, deBankUserBalanceApi } from "../utils";
import { useERC20Contract, useFsdContract } from '../utils/contractHelpers';

import "../assets/scss/Dashboard.scss";
import Bi_Direction from "../assets/imgs/Bi-Direction.svg";
import TotalSupply from "../assets/imgs/TotalSupply.svg";
import TotalValue from "../assets/imgs/TotalValue.svg";
import ETH from "../assets/imgs/TotalETH.svg";
import Capital from "../assets/imgs/CapitalEfficiency.svg";
import Holders from "../assets/imgs/Holders.svg";
import Statics from "../assets/imgs/Statics.svg";
import ArrowCircle from "../assets/imgs/Arrow-Circle.svg";

import { Option, Series, Token_Data } from "../config/variables";

const Dashboard = () => {
  const { isWalletConnected, account, web3 } = useSelector((state) => state.user);
  const fsd = useFsdContract();
  const { currentStakePhase } = useBasicData()
  const { ethBalance, fsdBalance, signature } = useAdvancedData(isWalletConnected, web3, account, fsd)
  const [options, setOptions] = useState(Option);
  const [series, setSeries] = useState(Series);

  const [tokens, setTokens] = useState([]);
  const [balance, setBalance] = useState(0);

  useEffect(() => {
    if (account) {
      console.log('account', account)
      deBankUserBalanceApi(account)
        .then(({ data: { usd_value } }) => {
          console.info('balance data', usd_value)
          setBalance(usd_value);

          deBankUserTokensListApi(account)
            .then(({ data }) => {
              console.info("token list data", data);
              let temp = [];
              for (let i in data) {
                const {
                  raw_amount,
                  amount,
                  id,
                  price,
                  logo_url,
                  decimals,
                  name,
                  symbol,
                } = data[i];
                temp.push({
                  address: id,
                  price,
                  logo_url,
                  decimals,
                  name,
                  symbol,
                  amount,
                  raw_amount,
                });
              }
              setTokens(temp);
            })
            .catch((error) => {
              console.error("user token list error");
            });
        })
        .catch((error) => {
          console.error("user balance error");
        });
    } else {
      setTokens([]);
    }
  }, [account]);

  return (
    <div className="dashboard">
      <div className="container-fluid">
        <div className="row g-0 card-wrapper">
          <div className="col-md card-content" style={{ marginRight: "30px" }}>
            <div className="container">
              <div className="row header">
                <div className="col-sm">
                  <p>Total ETH Locked</p>
                  <p>4460.14</p>
                </div>
                {/* <div className='col-sm'>
                  <p>Total Given</p>
                  <p>1350.1</p>
                </div>
                <div className='col-sm'>
                  <p>7 Day Change</p>
                  <p>+18.51%</p>
                </div> */}
              </div>
            </div>
            <div>
              <Chart
                options={options}
                series={series}
                type="area"
                height={260}
              />
            </div>
          </div>
          <div className="col-md card-content">
            <div className="wallet">
              <div className="overview">
                <p>Wallet Overview</p>
                <img src={Bi_Direction} alt="" width={"20px"} />
              </div>
              <div className="value">
                {/* <p>$325,614.14 <span>+6%</span></p> */}
                {/* <p>{(balance).toLocaleString('en-US', { style: 'currency', currency: 'USD' })}</p> */}
                {tokens.length ? (
                  <p>
                    {balance.toLocaleString("en-US", {
                      style: "currency",
                      currency: "USD",
                    })}
                  </p>
                ) : (
                  <p>
                    $325,614.14 <span>+6%</span>
                  </p>
                )}
              </div>
            </div>
            <div className="token-wrapper auto-scroll-y">
              {!tokens.length &&
                Token_Data.map((token, index) => (
                  <div className="token" key={index}>
                    <div className="token-header" key={index}>
                      <div className="token-name">
                        <img src={token.logo} alt="" />
                        <p>{token.name}</p>
                      </div>
                      <p>${token.value}</p>
                    </div>
                    <div className="progress">
                      <div
                        className="progress-bar"
                        style={{ width: token.progess + "%" }}
                      ></div>
                    </div>
                  </div>
                ))}
              {tokens.map((token, index) => (
                <div className="token" key={index}>
                  <div className="token-header" key={index}>
                    <div className="token-name">
                      <img src={token.logo_url} alt="" width={"18px"} />
                      <p>{token.symbol}</p>
                    </div>
                    <p>${(token.amount * token.price).toFixed(2)}</p>
                  </div>
                  <div className="progress">
                    <div
                      className="progress-bar"
                      style={{
                        width:
                          ((token.amount * token.price) / balance) * 100 + "%",
                      }}
                    ></div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
      <div className="bottom">
        <div className="title">
          <p className="sec-dark-color fs-21">Statistics</p>
          <img src={ArrowCircle} alt="" />
        </div>
        <div>
          <p className="description">
            Overview of static are updated on a 3 day period, or can be manually
            refreshed
          </p>
        </div>
        <div className="statics-card-wrapper">
          <div className="statics-card-content">
            <img src={TotalSupply} alt="" />
            <div>
              <p>Total Token Supply</p>
              <p className="sec-dark-color fs-21">20,425,156</p>
            </div>
          </div>
          <div className="statics-card-content">
            <img src={TotalValue} alt="" />
            <div>
              <p>Total USD Value</p>
              <p className="sec-dark-color fs-21">$30,516,123</p>
            </div>
          </div>
          <div className="statics-card-content">
            <img src={ETH} alt="" />
            <div>
              <p>Total ETH Locked</p>
              <p className="sec-dark-color fs-21">4160.14</p>
            </div>
          </div>
          <div className="statics-card-content">
            <img src={Capital} alt="" />
            <div>
              <p>Capital Efficiency</p>
              <p className="sec-dark-color fs-21">100%</p>
            </div>
          </div>
          <div className="statics-card-content">
            <img src={Holders} alt="" />
            <div>
              <p>Unique Holders</p>
              <p className="sec-dark-color fs-21">5,235</p>
            </div>
          </div>
          <div className="statics-card-content">
            <img src={Holders} alt="" />
            <div>
              <p>Total Amount Paid</p>
              <p className="sec-dark-color fs-21">20,425,156</p>
            </div>
          </div>
          <div className="statics-card-content">
            <img src={Holders} alt="" />
            <div>
              <p>Total Claims</p>
              <p className="sec-dark-color fs-21">$30,516,123</p>
            </div>
          </div>
        </div>
        <div className="container-fluid">
          <div className="statics-description-wrapper">
            <div
              className="col-md statics-description"
              style={{ marginRight: "30px" }}
            >
              <img src={Statics} alt="" />
              <div>
                <p className="sec-dark-color">
                  What is <span className="sec-dark-color">Fshare</span>?
                </p>
                <p>
                  The Capital Pool has a floating floor known as the FSHARE. It
                  measures the minimum capital requirement of the pool and is a
                  large component of token price and withdraw conditions.
                </p>
              </div>
            </div>
            <div className="col-md statics-description">
              <img src={Statics} alt="" />
              <div>
                <p className="sec-dark-color">
                  What is <span className="sec-dark-color">Capital Pool</span>?
                </p>
                <p>
                  The Capital Pool has a floating floor known as the FSHARE. It
                  measures the minimum capital requirement of the pool and is a
                  large component of token price and withdraw conditions.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
