import { useState, useEffect } from "react"

import SortDown from "../../assets/imgs/ArrowDropDownIcon.svg";
import SortUp from "../../assets/imgs/ArrowDropUpIcon.svg";
import ThreeDots from "../../assets/imgs/ThreeDots.svg";
import Approve from "../../assets/imgs/Approve.svg";
import Deny from "../../assets/imgs/Deny.svg";

export function ApprovedTable({ tableData, callback }) {
  const [isDateSort, setIsDateSort] = useState(true);
  const [isClaimSort, setIsClaimSort] = useState(true);
  const [isCSRSort, setIsCSRSort] = useState(true);
  const [table, setTable] = useState(tableData)

  useEffect(() => {
    let temp = tableData.map((t) => t);
    temp.sort(function (a, b) {
      return isClaimSort
        ? a.claim_status - b.claim_status
        : b.claim_status - a.claim_status;
    });
    setTable(temp);
  }, [isClaimSort]);

  useEffect(() => {
    let temp = tableData.map((t) => t);
    temp.sort(function (a, b) {
      return isCSRSort
        ? a.csr_status - b.csr_status
        : b.csr_status - a.csr_status;
    });
    setTable(temp);
  }, [isCSRSort]);

  const handleCallback = (event, fromEvent, data) => {
    callback(event, fromEvent, data)
  }

  return (
    <>
      <div>
        <table className="table table-hover">
          <thead>
            <tr>
              <td>Project</td>
              <td>Action</td>
              <td
                onClick={() => setIsDateSort(!isDateSort)}
                className="cursor-point"
              >
                Amount Paid{" "}
                <img
                  src={isDateSort ? SortDown : SortUp}
                  alt=""
                  className="ml-5"
                />
              </td>
              <td
                onClick={() => setIsClaimSort(!isClaimSort)}
                className="cursor-point"
              >
                # of claims{" "}
                <img
                  src={isClaimSort ? SortDown : SortUp}
                  alt=""
                  className="ml-5"
                />
              </td>
              <td
                onClick={() => setIsCSRSort(!isCSRSort)}
                className="cursor-point"
              >
                CSR Approved{" "}
                <img
                  src={isCSRSort ? SortDown : SortUp}
                  alt=""
                  className="ml-5"
                />
              </td>
            </tr>
          </thead>
          <tbody>
            {table.map((t, index) => (
              <tr key={index}>
                <td>
                  <img src={t.logo} alt="" className="mr-10" />
                  {t.project}
                </td>
                <td onClick={() => handleCallback("approve-event",'approved', t)}>
                  <p className="pri-color mb-0 cursor-point">CSR</p>
                </td>
                <td>15 ETH</td>
                <td>879</td>
                <td>
                  <img
                    src={
                      t.csr_status === 0
                        ? ThreeDots
                        : t.csr_status === 1
                          ? Deny
                          : Approve
                    }
                    alt=""
                    className="mr-10"
                  />
                  {t.csr_status === 0
                    ? "Pending"
                    : t.csr_status === 1
                      ? "Denied"
                      : "Approved"}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </>
  )
}