import { useState, useEffect } from 'react'
import { rpc, RPC_OPERATIONS } from '../utils'
import useRefresh from './useRefresh'

import { WEB3_RPC as web3 } from '../config'

import { cryptoCompareETHPriceApi } from '../utils'

import { MINTER_ADDRESS, FSD_ADDRESS, FSD_NETWORK_ADDRESS, ZAP_ADDRESS } from '../config'

import FSD_ABI from '../config/abis/FSD'
import FSD_NETWORK_ABI from "../config/abis/FSD_NETWORK";
import ZAP_ABI from "../config/abis/ZAP";
import MINTER_ABI from "../config/abis/MINTER";
import ERC20_ABI from "../config/abis/ERC20";

export const fsd = new web3.eth.Contract(FSD_ABI, FSD_ADDRESS)
export const fsdNetwork = new web3.eth.Contract(FSD_NETWORK_ABI, FSD_NETWORK_ADDRESS)

export const useBasicData = () => {

  const { slowRefresh } = useRefresh()

  const [tvl, setTvl] = useState('0')
  const [fsdPrice, setFsdPrice] = useState('0')
  const [currentStakePhase, setCurrentPhase] = useState(0)
  const [stakingAPR, setStakingAPR] = useState(0)
  const [volume, setVolumn] = useState(0)

  useEffect(() => {
    async function fetchBasicData() {
      const { fromWei, toBN: big, toWei } = web3.utils;
      const fundingPool = await rpc(fsd, "fundingPool", [], RPC_OPERATIONS.CALL);
      const TVL = big(await web3.eth.getBalance(fundingPool)).add(big(await web3.eth.getBalance(fsd._address)));
      const fsdPrice = await rpc(fsdNetwork, "getFSDPrice", [], RPC_OPERATIONS.CALL);
      const currentStakePhase = await rpc(fsd, "currentPhase", [], RPC_OPERATIONS.CALL);
      const GEARING_FACTOR = await rpc(fsd, "gearingFactor", [], RPC_OPERATIONS.CALL);
      const totalCostShareBenefits = await rpc(fsdNetwork, "totalCostShareBenefits", [], RPC_OPERATIONS.CALL);
      const totalOpenRequests = await rpc(fsdNetwork, "totalOpenRequests", [], RPC_OPERATIONS.CALL);
      const reserveBalance = await rpc(fsd, "getReserveBalance", [], RPC_OPERATIONS.CALL);
      const fShareRatio = parseInt(totalCostShareBenefits) === 0
        ? big(0)
        : big(reserveBalance)
          .sub(big(totalOpenRequests))
          .mul(big(10).pow(big(18)))
          .div(big(totalCostShareBenefits)).div(big(GEARING_FACTOR));
      const STAKING_REWARDS = toWei("0.20", "ether");
      const stakingAPR = fShareRatio.gte(big(toWei("1.25", "ether")))
        ? big(STAKING_REWARDS).add(fShareRatio).sub(big(toWei("1.25", "ether")))
        : big(STAKING_REWARDS);
      // let volume = 0;
      let usd;
      try {
        const { data: { USD } } = await cryptoCompareETHPriceApi()
        usd = USD
      } catch (err) {
        console.error(err.message)
        usd = 0;
      }
      const USDPricex1000 = big(usd * 1000);
      setTvl(parseFloat(fromWei(big(TVL).mul(USDPricex1000).div(big(1000)), "ether")).toFixed(4));
      setFsdPrice(parseFloat(fromWei(big(fsdPrice).mul(USDPricex1000).div(big(1000)), "ether")).toFixed(4));
      setCurrentPhase(currentStakePhase)
      setStakingAPR(fromWei(stakingAPR.mul(big(toWei("100", "ether"))).div(big(toWei("1", "ether"))), "ether"))
      setVolumn(0)
      console.info('useBasicData')
    }
    fetchBasicData()
  }, [slowRefresh])
  // }, [])

  return {
    volume,
    stakingAPR,
    fsdPrice,
    tvl,
    currentStakePhase
  }
}