import { useState } from 'react'
import { useSelector } from 'react-redux'
import { toast } from 'react-toastify';
import Modal from '../../components/Modal'
import Loader from '../../assets/imgs/loader.gif'

import Fire from '../../assets/imgs/Fire.svg'
import FSD_Logo from '../../assets/imgs/tokens/FSD.svg'
import ETH_Logo from '../../assets/imgs/tokens/ETH.svg'
import FairsideBG from '../../assets/imgs/Fairside-bg.svg'

import { useBasicData } from '../../hooks/useBasicData'
import { useAdvancedData } from '../../hooks/useAdvancedData';

import { RPC_OPERATIONS, rpc } from "../../utils";
import { useERC20Contract, useFsdContract, useFsdNetworkContract, useMinterContract, useZapContract } from '../../utils/contractHelpers';
import { stakePhases, funcsByStakingPhases, gasUsageByFunc, TokenData } from '../../config/variables';

const SignUpModalStyle = {
  padding: '',
  background: 'linear-gradient(90deg, #4569F5 0%, #4A6DF5 3%, #6885F7 29%, #7E97F8 54%, #8BA1F9 78%, #8FA5F9 100%)',
  border: '',
}

export function Unstaking() {
  const { isWalletConnected, account, web3 } = useSelector((state) => state.user);
  const fsd = useFsdContract();
  const [searchKey, setSearchKey] = useState('')
  const [isOpen, setIsOpen] = useState(false)
  const [isStakingStatusOpen, setIsStakingStatusOpen] = useState(false)
  const [selectedToken, setSelectedToken] = useState(TokenData[0])

  const [inputValue, setInputValue] = useState('')
  const [txHash, setTxHash] = useState()

  const [inputValueUnstake, setInputValueUnstake] = useState('0')
  const [tokensToBurn, setTokensToBurn] = useState('0')

  const [isPending, setIsPending] = useState(false)
  const { currentStakePhase } = useBasicData()
  const { ethBalance, fsdBalance, signature } = useAdvancedData(isWalletConnected, web3, account, fsd)

  const onChangeInputValueUnstake = async ({ target: { value } }) => {
    if (!isWalletConnected)
      return warning("Wallet not connected");

    const { toBN: big, toWei, fromWei } = web3.utils;

    const currentPhase = stakePhases[currentStakePhase];
    if (currentPhase !== "Final")
      return warning("Unstaking not allowed yet");

    if (!value)
      return setInputValueUnstake(value);


    if (isNaN(parseFloat(value)))
      return warning("Please provide valid value");

    setInputValueUnstake(value);

    let inputInWei;
    try { inputInWei = big(toWei(value, "ether")); }
    catch (e) { return warning(e.message()); }

    const fsdBalance = big(
      await rpc(
        fsd,
        "balanceOf",
        [account],
        RPC_OPERATIONS.CALL
      )
    );

    let tokensToBurn;
    try {
      tokensToBurn = big(
        await rpc(
          fsd,
          "getTokensBurned",
          [inputInWei],
          RPC_OPERATIONS.CALL
        )
      );
    } catch (e) { return warning(e.message); }

    if (fsdBalance.lt(tokensToBurn))
      return warning("Not enough FSD balance");

    setTokensToBurn(parseFloat(fromWei(tokensToBurn, "ether")).toFixed(4))

    let transactionCostUnstake;
    try {
      const gasEstimate = big(gasUsageByFunc["burn"]);
      // 	big(
      // 	await rpc(
      // 		fsd,
      // 		"burn",
      // 		[inputInWei, fsdBalance],
      // 		RPC_OPERATIONS.ESTIMATE,
      // 		{ from: account }
      // 	)
      // );
      const gasPrice = big(await web3.eth.getGasPrice());
      transactionCostUnstake = parseFloat(fromWei(gasEstimate.mul(gasPrice), "ether")).toFixed(4)
    } catch (e) {
      console.log(e.message)
      return warning(e.message);
    }

    // setTransactionCostUnstake(transactionCostUnstake)

  }

  const handleSubmitUnstake = async () => {
    if (!isWalletConnected)
      return warning("Wallet not connected");

    const { toWei, toBN: big } = web3.utils;

    const currentPhase = stakePhases[currentStakePhase];

    if (currentPhase !== "Final")
      return warning("Unstaking is not open yet!");

    let inputInWei;
    try { inputInWei = big(toWei(inputValueUnstake, "ether")); }
    catch (e) { return warning(e.message()); }

    const tokensToBurn = big(
      await rpc(
        fsd,
        "getTokensBurned",
        [inputInWei],
        RPC_OPERATIONS.CALL
      )
    );

    if (fsdBalance.lt(tokensToBurn))
      return warning("Not enough FSD balance");

    const { transactionHash: txHash } = await rpc(
      fsd,
      "burn",
      [inputInWei, fsdBalance],
      RPC_OPERATIONS.SEND,
      { from: account }
    );
    setTxHash(txHash)
    warning('Ether is unstaked and you can get them to your wallet by going to Claim tab and claiming')
  }

  function warning(str) {
    return toast.warning(<div>&nbsp;&nbsp;&nbsp;{str}</div>)
  }

  return (
    <>
      <div className='col-lg mt-md-3 mt-lg-0'>
        <div className='inch-card'>
          <div className='mt-lg-3 mt-md-0'>
            <p className='sec-dark-color fs-21'>
              {'Unstake to withdraw your ETH:'}
            </p>
            <div className='d-flex flex-row justify-content-start align-items-center gap-3'>
              <div className='inch-select'>
                <img src={FSD_Logo} alt='' style={{ 'marginLeft': 0 }} />
                FSD
              </div>
              <div className='pb-2'>
                <img src={Fire} alt='' />
              </div>
              <div className='inch-select'>
                <img src={ETH_Logo} alt='' style={{ 'marginLeft': 0 }} />
                ETH
              </div>
            </div>
            <>
              <div className='unstake-info'>
                <div className='d-flex justify-content-between'>
                  <p>ETH Received:</p>
                  <p className='pri-color'>3.3 ETH</p>
                </div>
                <div className='d-flex justify-content-between'>
                  <p>FSD Burned:</p>
                  <p className='pri-color'>303,431</p>
                </div>
              </div>
            </>

          </div>
          <div>
            <div className='d-flex flex-row justify-content-between inch-input-description'>
              <p>
                {'Amount of FSD to unstake'}
              </p>
              <p onClick={() => setInputValue(selectedToken.balanceOf)}>{`Available: ${isWalletConnected ? selectedToken.balanceOf : ' -'}`}</p>
            </div>
            <input type='number' value={inputValue} onChange={(e) => onChangeInputValueUnstake(e)} min={0} className='input-box' />
          </div>
          <div className='border-05 radius-10 p-10 d-flex flex-column mt-10'>
            <div className='d-flex justify-content-between'>
              <p className='mb-5'>Tribute Fee</p>
              <p className='mb-5 pri-color'>4.5%</p>
            </div>
            <div className='d-flex justify-content-between'>
              <p className='mb-5'>Conviction Score Loss</p>
              <p className='mb-5 pri-color'>-141</p>
            </div>
            <div className='d-flex justify-content-between'>
              <p className='mb-5'>ETH Received</p>
              <p className='mb-5 pri-color'>3.3 ETH</p>
            </div>
            <div className='d-flex justify-content-between'>
              <p className='mb-5'>FSD Burned</p>
              <p className='mb-5 pri-color'>303,431</p>
            </div>
          </div>
          <p className='inch-description mt-10'>
            {'Unstake/redeem your ERC-20 tokens and receive the equivalent amount of ETH after a few days. Withdraw fee will be included with the total.'
            }
          </p>
          <div className='inch-buttons'>
            <><button className='approved button-hover-shadow' onClick={() => handleSubmitUnstake()}>Unstake</button>
              <button className='disabled' disabled>Claim</button></>
          </div>
        </div>
      </div>      

      <Modal isOpen={isStakingStatusOpen} setIsOpen={setIsStakingStatusOpen} content_style={SignUpModalStyle}>
        <div className='join-alert'>
          <img src={FairsideBG} alt='' />
          <h4>Thank you for staking Fairside</h4>
          <p>Transaction ID: <a href={`https://etherscan.io/tx/${txHash}`} target='_blank' rel='noreferrer'>https://etherscan.io/tx</a></p>
        </div>
      </Modal>
    </>

  )
}