import React, { useRef, useState, useEffect } from 'react'
import styled, { css } from 'styled-components'
import ArrowDropDownIcon from '../assets/imgs/ArrowDropDownIcon.svg'

const DropDownHeader = styled.div`
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 16px;
  border-radius: 10px;
  transition: border-radius 0.15s;
  border: 1px solid #E1E1E1;
  background: #FFFFFF;
  p {
    margin: 0px;
  }
  &:focus {
    border: 1px solid var(--primary-color);
  }
`

const DropDownListContainer = styled.div`
  min-width: 136px;
  height: 0;
  position: absolute;
  overflow: hidden;
  transition: transform 0.15s, opacity 0.15s;
  transform: scaleY(0);
  transform-origin: top;
  opacity: 0;
  width: 100%;
  background: #FFFFFF;
  z-index: 10;
  P {
    margin: 0px
  }
`

const DropDownContainer = styled.div`
  cursor: pointer;
  width: ${({ width }) => width}px;
  position: relative;
  border-radius: 16px;
  height: 40px;
  min-width: 136px;
  user-select: none;


  ${(props) =>
    props.isOpen &&
    css`
      ${DropDownHeader} {
        border-radius: 10px 10px 0 0;
        border-bottom: 1px solid #E1E1E1;
      }

      ${DropDownListContainer} {
        height: auto;
        transform: scaleY(1);
        opacity: 1;
        border-top-width: 0;
        border-radius: 0 0 10px 10px;
        border: 1px solid #E1E1E1;
      }
    `}

  svg {
    position: absolute;
    right: 16px;
    top: 50%;
    transform: translateY(-50%);
  }
`

const DropDownList = styled.ul`
  padding: 0;
  margin: 0;
  box-sizing: border-box;
`

const ListItem = styled.li`
  list-style: none;
  padding: 8px 16px;
  &:hover {
    background:rgba(225, 225, 225, 0.5); 
  }
  display: flex;
  img {
    margin-left: 20px;
  }
`

const Select = ({ options, onChange }) => {
  const containerRef = useRef(null)
  const dropdownRef = useRef(null)
  const [isOpen, setIsOpen] = useState(false)
  const [selectedOptionIndex, setSelectedOptionIndex] = useState(0)
  const [containerSize, setContainerSize] = useState({ width: 0, height: 0 })

  const toggling = (event) => {
    setIsOpen(!isOpen)
    event.stopPropagation()
  }

  const onOptionClicked = (selectedIndex) => () => {
    setSelectedOptionIndex(selectedIndex)
    setIsOpen(false)

    if (onChange) {
      onChange(options[selectedIndex])
    }
  }

  useEffect(() => {
    setContainerSize({
      width: dropdownRef.current.offsetWidth, // Consider border
      height: dropdownRef.current.offsetHeight,
    })

    const handleClickOutside = () => {
      setIsOpen(false)
    }

    document.addEventListener('click', handleClickOutside)
    return () => {
      document.removeEventListener('click', handleClickOutside)
    }
  }, [])

  return (
    <DropDownContainer isOpen={isOpen} ref={containerRef} {...containerSize}>
      {containerSize.width !== 0 && (
        <DropDownHeader onClick={toggling}>
          <p>{options[selectedOptionIndex].label}</p>
          <img src={ArrowDropDownIcon} alt='' onClick={toggling}/>
        </DropDownHeader>
      )}
      <DropDownListContainer>
        <DropDownList ref={dropdownRef}>
          {options.map((option, index) =>
            index !== selectedOptionIndex ? (
              <ListItem onClick={onOptionClicked(index)} key={option.label}>
                <p color="textSubtle">{option.label}</p>
                {
                  option.icon && <img src={option.icon} alt=''/>
                }
              </ListItem>
            ) : null,
          )}
        </DropDownList>
      </DropDownListContainer>
    </DropDownContainer>
  )

}

export default Select