import { createSlice } from "@reduxjs/toolkit";

export const Status = {
  pending: 'pending',
  success: 'success',
  failed: 'failed'
}


const initialState = {
  hash: '',
  status: Status.pending,
  openModal: false
}

export const TransactionSlice = createSlice({
  name: 'transaction',
  initialState,
  reducers: {
    updateTransaction: (state, action) => {
      const { hash, status, openModal } = action.payload
      state.hash = hash;
      state.status = status;
      state.openModal = openModal
    },
  }
})

export const {
  updateTransaction
} = TransactionSlice.actions

export default TransactionSlice.reducer